  width: 0px;
  position: relative;
  right:30px;
}
.applybtn-content::-webkit-scrollbar{
  width: 10px;
  height: 50px !important;
  position: relative;
  right:30px;
}
.applybtn-content::-webkit-scrollbar-track {
  background: none; 
}
.regbtn-content::-webkit-scrollbar-track {
  background: none; 
}
/* Track */
.popup-content::-webkit-scrollbar-track {
  background: #ffffff88; 
}
 
/* Handle */
